:root {
    --radio-size: 20px;
    --navbar-bg: white;
    --navbar-height: 3rem;
    --sidebar-width: 4rem;
    --submenu-width: 16.75rem;
    --submenu-width-neg: -16.75rem;

    --color-primary: #232AED;
    --color-primary-highlight: #232AED;
    --color-primary-dark: #147350;
    --color-primary-hover: #d5f2e4;

    --color-border: #DDE4E8;

    --color-gray-50: #f2f2f2;
    --color-gray-100: #E5E5E5;
    --color-gray-300: #B2B2B2;
    --color-gray-500: #808080;
    --color-gray-700: #4D4D4D;
    --color-gray-900: #1A1A1A;
    --color-deep-dark-blue: #0E1A1F;

    --color-red: #e81f1f;
    --color-green: #54B14B;
    --color-orange: #ED672D;
    --color-blue: #2F60D7;
    --color-purple: #9234F5;
    --color-tomato: #d43646;


    --color-gray: #C6C6C6;
    --color-gray-lighter: #E7E9ED;
    --color-gray-light: #ACACAC;
    --color-gray-dark: #555555;
    --color-gray-darker: #333333;

    --box-shadow-button: rgba(0, 0, 0, 0.15) 0px 1px 1px 0px,
    rgba(220, 225, 230, 0.6) 0px 0px 0px 1px,
    rgba(220, 225, 230, 0.2) 0px 2px 4px 0px;

    --box-shadow-button-darker:
    rgba(0, 0, 0, 0.15) 0px 2px 2px 0px,
    rgba(220, 225, 230, 0.6) 0px 0px 0px 1px,
    rgba(220, 225, 230, 0.2) 0px 2px 4px 0px;
}

:root {

    --border-radius-default: .25rem;

    --color-white: #ffffff;
    /*--color-primary-50: #F3FBF5;*/
    --color-primary-50:  #cecfff;
    --color-primary-100: #bcbdf3;
    --color-primary-200: #ADEDB8;
    --color-primary-300: #34E574;
    --color-primary-400: #42A4FF;
    --color-primary-500: #232AED;
    --color-primary-600: #419A57;
    --color-primary-700: #2D32AD;
    --color-primary-800: #3B7146;
    --color-primary-900: #365F3E;
    --color-primary-1000: #045525;
	--header-primary: #242AB1;
    --color-cool-gray-10: #FDFDFD;
    --color-cool-gray-30: #F8F8F8;
    --color-cool-gray-50: #FDFDFD;
    --color-cool-gray-100: #f3f4f4;
    --color-cool-gray-200: #E7E8E9;
    --color-cool-gray-300: #B7BBBD;
    --color-cool-gray-400: #9FA5A7;
    --color-cool-gray-500: #878E91;
    --color-cool-gray-600: #6F777C;
    --color-cool-gray-700: #576166;
    --color-cool-gray-800: #3F4A50;
    --color-cool-gray-900: #27343a;
    --color-cool-gray-1000: #233b56;

    --color-blue-50: #F9FBFF;

    --color-secondary-50: #F5F9FF;
    --color-secondary-100: #E4EEFE;
    --color-secondary-200: #C7DCFE;
    --color-secondary-300: #A1C8FD;
    --color-secondary-500: #6DB1FD;
    --color-secondary-600: #1D9CF7;
    --color-secondary-700: #1887D6;
    --color-secondary-800: #1373B8;
    --color-secondary-900: #0E629E;
    --color-secondary-1000: #094B7C;


    /*--sidebar-width: 20rem;  --sidebar-width: 3.5rem; */
    --sidebar-width: 4.2rem;
    --sidebar-bg: var(--color-white);
    --sidebar-menu-item-height: 3rem;
    --sidebar-menu-item-name-color: var(--color-cool-gray-700);
    --sidebar-menu-item-font-weight: 400;

    --heading-line-height: 1.5;
    --heading-font-weight: 700;
    --heading-font-size: 1rem;
    --heading-font-color: var(--color-cool-gray-700);
    --heading-margin: 0;

    --grid-columns: repeat(auto-fill, minmax(20rem, 1fr));
    --grid-row-gap: 1rem;
    --grid-column-gap: 1rem;

    --grid-item-box-shadow: var(--box-shadow-button);
    --grid-item-border-radius: 0;
    --grid-item-background-color: transparent;

    --flair-margin-bottom: 0;
    --div-data-margin-left: 0;

    --separator-color: var(--color-cool-gray-300);
    --separator-size: 100%;
    --separator-margin: 1.5rem;

    --z-index-1: 1;
    --z-index-2: 2;
    --z-index-3: 3;
    --z-index-4: 4;
    --z-index-5: 5;
    --z-index-6: 6;
}


/* TOMATO ADMIN CSS */

.ta-container {
	width: 100%;
	height: 100%;
	background-color: var(--color-cool-gray-100);
}

.ta-dashboard {
	overflow-x: hidden;
	overflow-y: auto;
}

.ta-grid {
	display: grid;
	grid-auto-rows: 1fr;
	grid-row-gap: var(--grid-row-gap);
	grid-column-gap: var(--grid-column-gap);
	grid-template-columns: var(--grid-columns);
}

.ta-grid--item-no-bg {
	--grid-item-background-color: transparent;
}

.ta-grid--dashboard {
	--grid-columns: repeat(4, minmax(0, 1fr));
	/*--grid-columns: repeat(auto-fill, minmax(24rem, 1fr));*/
	--grid-item-box-shadow: var(--box-shadow-button);
	--grid-item-background-color: white;
	--grid-item-border-radius: var(--border-radius-default);
	margin: 0 1rem;
}

@media only screen and (max-width: 96rem) {
	.ta-grid--dashboard {
		--grid-columns: repeat(auto-fill, minmax(24rem, 1fr));
	}
}

.ta-grid--dashboard .highcharts-yaxis-grid .highcharts-grid-line:last-child {
	display: none;
}

.ta-grid--dashboard-calendar-detail {
	--grid-columns: auto 1fr;
	--grid-row-gap: 1rem;
	--grid-column-gap: 3rem;
	--grid-item-box-shadow: none;
	--grid-item-background-color: transparent;
	--grid-item-border-radius: 0;
}

.ta-grid--dashboard::before {
	content: '';
	width: 0;
	padding-bottom: 100%;
	grid-row: 1 / 1;
	grid-column: 1 / 1;
}

.ta-grid--dashboard > *:first-child {
	grid-row: 1 / 1;
	grid-column: 1 / span 2 !important;
}

.ta-grid__item {
	display: flex;
	flex-direction: column;
	width: 100%;
	overflow: hidden;
	background-color: var(--grid-item-background-color);
	box-shadow: var(--grid-item-box-shadow);
	border-radius: var(--grid-item-border-radius);
}

.ta-grid__item__content--dashboard-sales {
	--grid-item-box-shadow: none;
	--grid-row-gap: 1.5rem;
	--grid-columns: repeat(2, minmax(0, 1fr));
	--flair-margin-bottom: .75rem;
	--div-data-margin-left: .5rem;

	margin-top: .5rem;
}

.ta-grid__item__content--dashboard-mart-sales {
	background-image: url('/common/images/illusts/dashboard/mart-sales.svg');
}

.ta-grid__item__content--dashboard-app-sales {
	background-image: url('/common/images/illusts/dashboard/app-sales.svg');
}

.ta-grid__item--dashboard-sales .ta-grid {
	margin-top: .5rem;
}

.ta-grid__item__whisper {
	margin-top: auto;
	display: flex;
	flex-direction: column;
}

.ta-grid__item__whisper__headline {
	font-size: 1rem;
	line-height: calc(1rem * 1.5);
}

.ta-grid__item__whisper__cta {
	color: var(--color-secondary-600);
	line-height: calc(.875rem * 1.5);
}

.ta-grid__item--size-large {
	grid-column: span 2;
}

.ta-grid__item--row {
	flex-direction: row;
}

.ta-grid__item--split-half {
	--grid-item-box-shadow: none;
	--grid-item-background-color: transparent;
	--grid-item-border-radius: 0;

	position: relative;
	flex: 0 0 50%;
}

.ta-grid__item--split-half:last-child:before {
	position: absolute;
	content: '';
	top: 3rem;
	left: 0;
	width: 1px;
	height: calc(100% - 4rem);
	background-color: var(--color-cool-gray-100)
}

.ta-grid__item__header {
	display: flex;
	justify-content: space-between;
	padding: 1rem;
	align-items: center;
	height: 3rem;
}

.ta-grid__item__header__heading {
	font-weight: 500;
	font-size: .875rem;
	color: black;
}

.ta-grid__item__header__cta {
	color: var(--color-secondary-600);
	font-size: .875rem;
}

.ta-dashboard__header {
	display: flex;
	justify-content: space-between;
	padding: 1.375rem 1rem;
}

.ta-dashboard__header__heading {
	font-weight: 400;
	font-size: 1.5rem;
	line-height: 2.25rem;
	color: black;
}

.ta-grid__item--color-gradient-secondary {
	background: linear-gradient(0deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0,  0.1)),
				linear-gradient(180deg, #1380E5 0%, #1A2CD4 100%);
	--dashboard-news-color: white;
	--separator-color: rgba(255, 255, 255, .6);
}

.ta-grid__item--color-gradient-secondary * {
	color: white;
}

.ta-grid__item__content {
	position: relative;
	display: flex;
	flex-direction: column;
	flex: 1;
	padding: 0 1rem 1rem;
	height: 1px;
	min-height: 1px;
	min-width: 1px;
	overflow: hidden;
	background-position: right 1rem bottom 1rem;
	background-repeat: no-repeat;
	background-size: calc(100% / 4);
}

.ta-flair {
	--flair-font-size: .875rem;
	font-size: var(--flair-font-size);
	background-color: var(--color-blue-50);
	border-radius: var(--border-radius-default);
	color: var(--color-cool-gray-1000);
	line-height: calc(var(--flair-font-size) * 1.5);
	padding: .25rem .5rem;
	font-weight: 700;
	width: max-content;
	margin-bottom: var(--flair-margin-bottom);
	white-space: nowrap;
}

.ta-flair--secondary {
	background-color: var(--color-secondary-100);
	color: var(--color-secondary-1000);
	font-weight: 700;
}

.ta-flair--small {
	padding: .125rem .5rem;
}

.ta-flair--tiny {
	--flair-font-size: .75rem;
	padding: .125rem .25rem;
}

.ta-flair--important {
	background-color: var(--color-red);
	color: white;
}

.ta-flair--primary {
	background-color: var(--color-primary-50);
	color: var(--color-primary-1000);
}

.ta-data {
	display: flex;
	margin-left: var(--div-data-margin-left);
}

.ta-data__main {
	color: var(--color-cool-gray-1000);
	font-size: 1.5rem;
	font-weight: 700;
}

.ta-data__sub {
	font-size: .875rem;
	color: var(--color-gray-500);
	margin-left: 1ch;
	align-self: flex-end;
	margin-bottom: 1px
}

.ta-dashboard__board {
	--dashboard-news-color: var(--color-cool-gray-1000);
	color: var(--dashboard-news-color);
}

.ta-dashboard__board__post {
	display: flex;
	flex-direction: column;
}

.ta-dashboard__board__post + .ta-dashboard__board__post {
	margin-top: 1rem;
}

.ta-dashboard__board__header {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.ta-dashboard__board__eyebrow {
	opacity: 70%;
	font-size: .8125rem;
	margin-bottom: .25rem;
}

.ta-dashboard__board__title {
	font-weight: 700;
	font-size: .875rem;
	line-height: 1.5;
	color: var(--color-cool-gray-700);
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.ta-dashboard__board__message {
	opacity: 70%;
	height: calc(.875rem * 1.5 * 3);
	line-height: calc(.875rem * 1.5);
	max-height: 6rem;
	margin-bottom: .5rem;
	font-weight: 500;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 3;
	overflow: hidden;
}

.ta--dashboard__board__cta {
	opacity: 70%;
	margin-left: auto;
	font-weight: 400;
}

.ta-separator {

}

.ta-separator--horizontal {
	height: 1px;
	width: var(--separator-size);
	background: var(--separator-color);
	margin: 1.5rem 0;
}


.ta-calendar {
	display: grid;
	grid-template-columns: repeat(7, minmax(0, 1fr));
	grid-auto-rows: 1fr;
	grid-gap: 1px;
}

.ta-calendar--dashboard {
	font-size: 1.25rem;
}

/*.ta-calendar::before {*/
/*	content: '';*/
/*	width: 0;*/
/*	padding-bottom: 100%;*/
/*	grid-row: 1 / 1;*/
/*	grid-column: 1 / 1;*/
/*}*/

/*.ta-calendar > *:first-child {*/
/*	grid-row: 1 / 1;*/
/*	grid-column: 1 / 1*/
/*}*/

.ta-calendar--month {
	grid-template-columns: repeat(3, minmax(0, 1fr));
}

.ta-calendar__day {
	display: grid;
	place-items: center;
	color: var(--color-cool-gray-500);
	font-weight: 500;
	padding: .75rem;
	border-radius: .25rem;
	position: relative;
	min-width: 2.75rem;
	min-height: 2.75rem;
}

.ta-calendar__day:hover {
	font-weight: 700;
}

.ta-calendar__day--not-this-month {
	opacity: .4;
}

.ta-calendar__day--selected {
	color: var(--color-primary-500);
	background-color: var(--color-primary-100);
	font-weight: 700;
}

.ta-calendar__day--holiday {
	color: var(--color-red);
}

.ta-calendar-dotw {
	display: flex;
	width: 100%;
}

.ta-calendar-dotw__item {
	flex: 1 1 auto;
	font-weight: 500;
	font-size: .875rem;
	color: var(--color-cool-gray-700);
	text-align: center;
	padding: .25rem 0;
}

.ta-calendar__from-to-select {
	display: flex;
	align-items: center;
	justify-content: space-between;
	line-height: 1.5;
	margin-bottom: .25rem;
}

.ta-calendar__from-to {
	display: flex;
	flex-direction: column;
	padding: .25rem .625rem;
	--eyebrow-color: var(--color-cool-gray-300);
	--date-color: var(--color-cool-gray-500);
	cursor: pointer;
	min-height: 2.5625rem;
}

.ta-calendar-from-to--selected {
	background-color: var(--color-primary-50);
	--eyebrow-color: var(--color-primary-700);
	--date-color: var(--color-primary-700);
}

.ta-calendar__from-to__eyebrow {
	font-size: .6125rem;
	color: var(--eyebrow-color);
}

.ta-calendar__from-to__date {
	font-weight: 700;
	color: var(--date-color);
}

.ta-calendar__year-month {
	padding: .25rem;
	display: flex;
	align-items: center;
	position: relative;
	height: 2rem;
	justify-content: space-between;
	margin-bottom: .25rem;
}

.ta-calendar__year-month__cta {
	padding: .125rem .5rem;
	font-weight: 700;
	color: var(--color-cool-gray-700);
	border-radius: .25rem;
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	white-space: nowrap;
	line-height: 2;
}

.ta-calendar__year-month__cta:hover {
	background-color: var(--color-cool-gray-100);
	color: inherit;
}

.ta-calendar__shortcuts {
	border-left: 1px solid var(--color-cool-gray-200);
	padding-left: .5rem;
	margin-left: .5rem;
}

.ta-calendar__shortcuts--left {
	border: 0;
	border-right: 1px solid var(--color-cool-gray-200);
	padding: 0;
	margin: 0;
	padding-right: .5rem;
	margin-right: .5rem;
}

.ta-calendar__shortcuts__list {
	font-size: .75rem;
}

.ta-calendar__shortcuts__item {
	line-height: 1.875rem;
	color: var(--color-cool-gray-500);
	padding: 0 .625rem;
	border-radius: .25rem;
	cursor: pointer;
}

.ta-calendar__shortcuts__item:hover {
	background-color: var(--color-primary-50);
	color: var(--color-primary-1000);
	font-weight: 500;

}

.ta-calendar__shortcuts__anchor:hover {
	color: inherit;
}

.ta-calendar__shortcuts__list--year .ta-calendar__shortcuts__item {
	min-width: 3.5rem;
	text-align: center;
}

.ta-calendar--small {
	font-size: .75rem;
    min-width : 230px;
}

.ta-calendar--small .ta-calendar-dotw__item {
	font-size: .75rem;
}

.ta-calendar--small .ta-calendar__day {
	min-height: 2rem;
	min-width: 2rem;
	padding: .125rem;
}

.ta-calendar__day--disabled {
	cursor: not-allowed;
	color: var(--color-gray-100);
}

.ta-calendar__day--disabled:hover {
	color: var(--color-gray-100);
	font-weight: 400;
}


.ta-button {
	display: flex;
	align-items: center;
	justify-content: center;
}

.ta-button--square {
	position: relative;
}

.ta-eyebrow {
	font-size: .8125rem;
	line-height: 1.5;
	color: var(--color-cool-gray-500);
}

.ta-heading {
	/*****************************/
	/* HEADING VARIABLE TEMPLATE */
	/*****************************/
	/*--heading-font-color:*/
	/*--heading-line-height:*/
	/*--heading-font-weight:*/
	/*--heading-font-size:*/
	/*--heading-margin:*/

	color: var(--heading-font-color);
	line-height: var(--heading-line-height);
	font-weight: var(--heading-font-weight);
	font-size: var(--heading-font-size);
	margin: var(--heading-margin);
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.ta-grid__item__calendar-detail {
	display: flex;
	flex-direction: column;
	padding-bottom: 1rem;
	position: relative;
}

.ta-grid__item__calendar-detail:last-child {
	margin-bottom: .1rem;
}

.ta-grid__item__calendar-detail:last-child:before {
	position: absolute;
	bottom: 0;
	content: '';
	background-color: var(--color-cool-gray-300);
	height: 1px;
	width: clamp(.5rem, 2.25rem, 10%);
}

.ta-sidebar {
	position: absolute;
	top: 0;
	left: 0;
	width: var(--sidebar-width);
	height: 100%;
	overflow: hidden;
	background-color: var(--color-white);
	z-index: var(--z-index-2);
	transform: translateX(calc(var(--sidebar-width) * -1));
	will-change: transform;
	transition: transform .2s ease-in-out;
}

.ta-sidebar--active {
	transform: translateX(0);
	box-shadow: 1px 0 12px 1px #00000014;
}

.ta-sidebar__wrapper {
	display: flex;
	width: calc(var(--sidebar-width) * 2);
	will-change: transform;
	transform: translateX(calc(var(--sidebar-width) * -1));
	transition: transform .2s ease-in-out;
	height: 100%;
}

.ta-sidebar__wrapper--swapped {
	transform: translateX(0);
}

.ta-sidebar__container {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 100%;
	width: var(--sidebar-width);
}

.ta-sidebar__menu {
	display: block;
}

.ta-sidebar__menu--sub {
	max-height: 0;
	transition: max-height .1s ease-out;
	will-change: max-height;
	overflow: hidden;
}
.ta-sidebar__menu--sub:last-child{
	margin-bottom: 3.75rem;

}

.ta-sidebar__menu--sub__container {
	padding-bottom: 1px solid var(--color-cool-gray-100);
	border-bottom: 1px solid var(--color-primary-400);
}

.ta-sidebar__menu--sub + .ta-sidebar__menu--sub {
	margin-top: .5rem;
	border-top: 1px solid var(--color-cool-gray-200);
}

.ta-sidebar__menu__item {
	display: flex;
	align-items: center;
	height: var(--sidebar-menu-item-height);
	white-space: nowrap;
	padding: 0 1rem;
}

.ta-sidebar__menu__item--selected {
	--sidebar-menu-item-name-color: var(--color-cool-gray-1000);
	background-color: var(--color-cool-gray-100);
}

.ta-sidebar__menu__item--category {
	--sidebar-menu-item-height: 2rem;
}

.ta-sidebar__menu__item--sub-category {
	--sidebar-menu-item-height: 2.25rem;
	--sidebar-menu-item-name-color: var(--color-cool-gray-700);
	--sidebar-menu-item-font-weight: 500;

	font-size: .75rem;
	line-height: 1.5;
	justify-content: space-between;
	border-bottom: 1px solid var(--color-cool-gray-200);
}

.ta-sidebar__menu__item--sub-category--active {
	--sidebar-menu-item-name-color: var(--color-cool-gray-900);
	border-bottom: 1px solid var(--color-cool-gray-200);
}

.ta-sidebar__menu__item--sub-category--active .ta-sidebar__chevron {
	transform: rotate(180deg);
}

.ta-sidebar__chevron {
	transition: .1s transform ease-in;
	will-change: transform;
	width: .875rem;
	height: .875rem;
}

.ta-sidebar__menu__item--sub-category--active + .ta-sidebar__menu--sub {
	max-height: 100vh;
}

.ta-sidebar__menu__item--sub-category:hover {
	--sidebar-menu-item-name-color: var(--color-cool-gray-1000);
	background-color: var(--color-secondary-50);
}

.ta-sidebar__menu__item--header {
	background-color: var(--color-cool-gray-100);
}

.ta-sidebar__menu__item--page {
	--sidebar-menu-item-height: 2rem;
	--sidebar-menu-item-name-color: var(--color-cool-gray-900);
	--sidebar-menu-item-font-weight: 500;
	align-items: center;
}

.ta-sidebar__menu__item--page:hover {
	--sidebar-menu-item-name-color: var(--color-cool-gray-1000);
	background-color: var(--color-cool-gray-100);
}

.ta-sidebar__menu__item--category:hover {
	background-color: var(--color-primary-50);
	border-color: transparent;
}

.ta-sidebar__menu__item__glyph {
	display: grid;
	place-items: center;
	height: var(--sidebar-menu-item-height);
	width: var(--sidebar-menu-item-height);
}

.ta-sidebar__menu__item__name {
	font-weight: var(--sidebar-menu-item-font-weight);
	color: var(--sidebar-menu-item-name-color);
	margin-right: .25rem;
	white-space: nowrap;
}

.ta-sidebar__menu__item__cta {
	background: url(/common/images/glyphs/glyph-chevron-right.svg) no-repeat center center;
	background-size: .75rem .75rem;
